<template>
    <div class="col-lg-12 mt-3 p-3">
        <div class="heading-border mb-2">
            <h5 class="h5">
                <span>Add Quotes</span>
                <small class="float-right">
                    <a href="javascript:;" @click="addRow()">
                        <i class="fa fa-plus pr-1"></i>Add Row
                    </a>
                </small>

            </h5>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 " v-for="(item, index) in newDynamicField"
            v-bind:key="index">
            <div class="row addQuoteRows">
                <div class="col-lg-3">
                    <div class="form-group">

                        <label>Vendor:<span class="text-danger">*</span></label>
                        <div class="input-group">
                            <Field :name="'Vendor' + (index + 1)" class="w-100" :rules="{ 'required': true }"
                                v-slot="{ errors }">
                                <select name="Vendor" v-model="item.getVenderValue"
                                    v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }">
                                    <option value="">Select</option>
                                    <option v-for="item in venderList" :value="item.Value" :key="item">
                                        {{ item.Text }}
                                    </option>
                                </select>
                                <span v-if="errors[0]" :name="'Vendor' + (index + 1)" class="invalid-feedback d-block">{{
                                    errors[0] }}</span>
                            </Field>
                        </div>



                    </div>
                </div>

                <div class="col-lg-2">
                    <div class="form-group">
                        <label>Requested Quantity:<span class="text-danger">*</span></label>
                        <div class="input-group">

                            <Field :name="'RequestQuantity' + (index + 1)" :rules="{ 'required': true }"
                                v-slot="{ errors }">
                                <input v-model="item.txtrequestQuantity" type="text"
                                    v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                <span v-if="errors[0]" :name="'RequestQuantity' + (index + 1)"
                                    class="invalid-feedback d-block">{{
                                        errors[0] }}</span>
                            </Field>
                        </div>
                    </div>
                </div>
                <div class="col-md-1 ">
                    <div class="form-group">
                        <label>Currency:<span class="text-danger">*</span></label>
                        <div class="input-group">

                            <Field :name="'Currency' + (index + 1)" class="w-100" :rules="{ 'required': true }"
                                v-slot="{ errors }">
                                <select :name="'Currency' + (index + 1)" v-model="item.getCurrencyValue"
                                    v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }">
                                    <option value="">Select</option>

                                    <option v-for="(item, currencyIndex) in currencyList" :value="item.CurrencyId"
                                        :key="currencyIndex">
                                        {{ item.Currency }}
                                    </option>
                                </select>
                                <span v-if="errors[0]" :name="'Currency' + (index + 1)" class="invalid-feedback d-block">{{
                                    errors[0] }}</span>
                            </Field>

                        </div>

                    </div>
                </div>
                <div class="col-lg-1 pl-0">
                    <div class="form-group">
                        <label>Unit Price:<span class="text-danger">*</span></label>

                        <Field :name="'UnitPrice' + (index + 1)" :rules="{ 'required': true }" v-slot="{ errors }">
                            <input v-model="item.txtUnitPrice" type="text" v-on:keyup="calculateTotalQuantity(item)"
                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                            <span v-if="errors[0]" :name="'UnitPrice' + (index + 1)" class="invalid-feedback d-block">{{
                                errors[0] }}</span>
                        </Field>


                    </div>
                </div>
                <div class="col-lg-1 pl-0">
                    <div class="form-group">
                        <label>Total Price:<span class="text-danger">*</span></label>

                        <input :name="'totalPrice' + (index + 1)" :id="'totalPrice' + (index + 1)"
                            :value="item.txtTotalPrice" type="text" disabled class="form-control " />

                    </div>
                </div>
                <div class="col-lg-3">

                    <div class="form-group">
                        <label>Upload Quote:</label>

                        <div class="input-group">

                            <div class="input-group">
                                <div class="custom-file">
                                    <input type="file" :name="'FileSrc' + (index + 1)" :id="'flFile' + (index + 1)"
                                        data-file="multi" tabindex="-1" @change="setFile($event, index)"
                                        style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                    <div class="bootstrap-filestyle input-group">
                                        <input type="text" class="form-control disabled" disabled=""
                                            :value="item.uploadQuote" />
                                        <div class="input-group-append" style="cursor:pointer;">
                                            <span class="group-span-filestyle input-group-btn input-group-text bg-white"
                                                tabindex="0">
                                                <label title="Click here to select a file." :for="'flFile' + (index + 1)"
                                                    class="mb-0 font-weight-normal ">Choose File</label>
                                            </span>
                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                <a href="javascript:;" class="round-icon-small btn-danger-light"
                                                    title="Clear" @click="Clear()"><i class="fa fa-undo"></i></a>

                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>



                        </div>

                        <!-- <div class="form-group">
                            <input type="file" multiple class="form-control input-group-btn input-group-text bg-white"
                                required />
                        </div> -->

                    </div>
                </div>

                <div class="col-lg-1 mt-4">
                    <a href="javascript:void;" @click="removeRow(item.count);"
                        class="btn btn-danger theme-primary removefield">
                        <i class="fa fa-times pt-7" title=""></i>
                    </a>

                </div>

            </div>

        </div>
    </div>
</template>
<script>
import DataService from "../../service/DataService";
export default {

    data() {
        return {

            newDynamicField: [{
                getVenderValue: '',
                txtrequestQuantity: '',
                getCurrencyValue: '',
                txtUnitPrice: '',
                txtTotalPrice: '',
                uploadQuote: '',
                count: 1
            }],
            quotes: [],
            specificCount: null,
            venderList: [],
            currencyList: [],
            purchases: [],
            files: []
        }
    },

    async created() {
        var vm = this;
        await vm.getVendorlist();
        await vm.getCurrencylist();

    },

    updated() {
        debugger;
        var vm = this;
        vm.newDynamicField;

    },

    methods: {

        setFile: function (e, item) {
            debugger;
            var vm = this;

            vm.newDynamicField.forEach(element => {
                const specificRecord = vm.newDynamicField.find(x => x.count === item + 1);
                if (specificRecord != null) {
                    specificRecord.uploadQuote = e.target.files[0].name;
                }
            })

            console.log(vm.newDynamicField);

            this.files.push(e.target.files[0]);

        },

        getVendorlist() {

            DataService.GetCrmVendor().then((response) => {
                if (response.data != null) {
                    this.venderList = response.data;
                }
            })

        },

        getCurrencylist() {

            DataService.GetCurrency().then((response) => {
                if (response.data != null) {
                    this.currencyList = response.data;
                }
            })
        },



        calculateTotalQuantity(item) {
            debugger;
            console.log("item", item);
            var vm = this;

            vm.newDynamicField.forEach(element => {

                const specificRecord = vm.newDynamicField.find(x => x.count === item.count);
                if (specificRecord != null) {
                    specificRecord.txtTotalPrice = item.txtrequestQuantity * item.txtUnitPrice;
                }


            })

            console.log(vm.newDynamicField);

        },




        addRow() {
            var vm = this;
            let quote = {};
            const specificRecord = vm.newDynamicField.find(x => x.count === vm.specificCount);
            if (specificRecord != null) {
                quote = {
                    quoteId: 0,
                    quoteVendor: specificRecord.getVenderValue,
                    quoteQuantity: specificRecord.txtrequestQuantity,
                    quoteAmount: specificRecord.txtUnitPrice,
                    quotePrice: specificRecord.getCurrencyValue,
                    uploadQuote: specificRecord.uploadQuote
                }

                vm.quotes.push(quote);
                vm.specificCount = null;
            }


            let singleDynamicRow = {
                getVenderValue: '',
                txtrequestQuantity: '',
                getCurrencyValue: '',
                txtUnitPrice: '',
                uploadQuote: '',
                count: 1
            }

            singleDynamicRow.count = vm.newDynamicField.length + 1;
            vm.newDynamicField.push(singleDynamicRow);
            console.log(vm.newDynamicField);

        },

        removeRow(event) {
            debugger;
            var vm = this;
            const index = vm.newDynamicField.findIndex(x => x.count === event);
            if (index !== -1) {
                vm.newDynamicField.splice(index, 1);
            }
            return true
        }

    }

}


</script>
<style ></style>