<template >
    <div class="col-lg-12 mt-3 p-3">
        <div class="heading-border mb-2">
            <h5 class="h5">
                <span id="TabHeading2">Online Purchase</span>
                <small class="float-right">
                    <a href="javascript:;" class="addPurchaseLinkRows" title="" @click="showRow">
                        <i class="fa fa-plus pr-1"></i>Add Row
                    </a>
                </small>
            </h5>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 " v-show="isVisible">
            <div class="row addQuoteRows">
                <div class="col-lg-3">
                    <div class="form-group">

                        <label>Vendor:<span class="text-danger">*</span></label>
                        <div class="input-group">
                            <Field name="Vendor" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                <select name="Vendor" v-model="purchase_vendor"
                                    v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }">
                                    <option value="">Select</option>
                                    <option v-for="item in venderList" :value="item.Value" :key="item">
                                        {{ item.Text }}
                                    </option>
                                </select>
                                <span v-if="errors[0]" name="Vendor" class="invalid-feedback d-block">{{
                                    errors[0] }}</span>
                            </Field>
                        </div>



                    </div>
                </div>

                <div class="col-lg-2">
                    <div class="form-group">
                        <label>Requested Quantity:<span class="text-danger">*</span></label>
                        <div class="input-group">

                            <Field name="Request Quantity" :rules="{ 'required': true }" v-slot="{ errors }">
                                <input v-model="txtpurchaseQuantity" type="text"
                                    v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                <span v-if="errors[0]" name="Request Quantity" class="invalid-feedback d-block">{{
                                    errors[0] }}</span>
                            </Field>
                        </div>
                    </div>
                </div>
                <div class="col-md-1 ">
                    <div class="form-group">
                        <label>Currency:<span class="text-danger">*</span></label>
                        <div class="input-group">

                            <Field name="Currency" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                <select name="Currency" v-model="getCurrencyValue"
                                    v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }">
                                    <option value="">Select</option>

                                    <option v-for="(item, currencyIndex) in currencyList" :value="item.CurrencyId"
                                        :key="currencyIndex">
                                        {{ item.Currency }}
                                    </option>
                                </select>
                                <span v-if="errors[0]" name="Currency" class="invalid-feedback d-block">{{
                                    errors[0] }}</span>
                            </Field>

                        </div>

                    </div>
                </div>
                <div class="col-lg-1 pl-0">
                    <div class="form-group">
                        <label>Unit Price:<span class="text-danger">*</span></label>

                        <Field name="Unit Price" :rules="{ 'required': true }" v-slot="{ errors }">
                            <input v-model="priceAmount" type="text" v-on:keyup="calculateTotalQuantity()"
                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                            <span v-if="errors[0]" name="Unit Price" class="invalid-feedback d-block">{{
                                errors[0] }}</span>
                        </Field>


                    </div>
                </div>
                <div class="col-lg-1 pl-0">
                    <div class="form-group">
                        <label>Total Price:<span class="text-danger">*</span></label>
                        <input v-model="purchasePrice" type="text" disabled class="form-control TotalPrice" />

                    </div>
                </div>
                <div class="col-lg-3 " v-for="(item, index) in purchaseDynamicField" :key="item">

                    <div class="form-group">
                        <label>Purchase Link<span class="text-danger">*</span></label>
                        <div class="input-group">
                            <input type="text" v-model="item.purchase_link" class="form-control" name="purchaseLink">
                            <div class="input-group-append" style="cursor:pointer;">
                                <span class="group-span-filestyle input-group-btn input-group-text bg-grey">
                                    <a title="Click here to copy link." class="mb-0 font-weight-normal "><i
                                            class="fa fa-copy "></i></a>
                                </span>

                                <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                    <a href="javascript:void(0);" @click="addLinkRow(item)" v-if="0 == parseInt(index)"
                                        class="round-icon-small btn-dark theme-primary round-icon-small">
                                        <i class="fa fa-plus pt-7"></i>
                                    </a>
                                    &nbsp;

                                    <a href="javascript:void;" @click="removeRow(item.count);"
                                        class="btn btn-danger theme-primary removefield">
                                        <i class="fa fa-times pt-7" title=""></i>
                                    </a>

                                </span>

                            </div>
                        </div>

                    </div>
                </div>

            </div>

        </div>
    </div>
</template>
<script>
import DataService from "../../service/DataService";
export default {

    data() {
        return {

            isVisible: false,
            purchase_vendor: '',
            txtpurchaseQuantity: '',
            getCurrencyValue: '',
            purchasePrice: '',
            priceAmount: '',
            purchaseDynamicField: [
                {
                    purchase_link_id: 0,
                    purchase_link: '',
                    count: 0
                }
            ]

            ,
            specificCount: null,
            venderList: [],
            currencyList: [],
            purchases: []
        }
    },

    created() {
        var vm = this;
        vm.getVendorlist();
        vm.getCurrencylist();

    },
    methods: {

        getVendorlist() {

            DataService.GetCrmVendor().then((response) => {
            
                if (response.data != null) {
                    this.venderList = response.data;
                }
            })

        },

        getCurrencylist() {

            DataService.GetCurrency().then((response) => {
                if (response.data != null) {
                    this.currencyList = response.data;
                }
            })
        },



        calculateTotalQuantity() {
            var vm = this;
            vm.purchasePrice = vm.txtpurchaseQuantity * vm.priceAmount

        },

        showRow() {
            var vm = this;
            vm.isVisible = true;
        },

        addLinkRow(event) {

            var vm = this;
            debugger;
            vm.specificCount = event.count;
            vm.isVisible = true;
            let purchase = {}
            const specificRecord = vm.purchaseDynamicField.find(x => x.count === vm.specificCount);
            if (specificRecord != null) {

                purchase = {
                    purchase_link_id: 0,
                    purchase_link: event.purchase_link,

                }

                vm.purchases.push(purchase);
                vm.specificCount = null;
            }



            let singleDynamicRow = {
                purchase_link_id: 0,
                purchase_link: '',
                count: vm.purchases.length + 1
            }


            singleDynamicRow.count = vm.purchaseDynamicField.length + 1;
            vm.purchaseDynamicField.push(singleDynamicRow);


        },

        removeRow(event) {
            debugger;

            var vm = this;
            const index = vm.purchaseDynamicField.findIndex(x => x.count === event);
            if (index !== -1) {
                vm.purchaseDynamicField.splice(index, 1);
            }
          
            return true
        }

    }

}


</script>
<style ></style>